import React, { useEffect, useState } from 'react'
import _ from 'lodash';
import { Link, useParams, useLocation } from 'react-router-dom';
import {
    useModal,
    Button
} from '@pancakeswap-libs/uikit'
import BigNumber from 'bignumber.js'
import { useLifeContract } from 'hooks/useContract'
import nfts, { NftFarm, NftLiteMarketPlace } from 'config/constants/genesisNfts';
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { getLifeAddress } from 'utils/addressHelpers'
import { useNftAllowance } from '../../hooks/useAllowance'
import BuyNftModal from './components/BuyNftModal'
import SellNftModal from './components/SellNftModal'
import { getNftLiteMarketplaceContract } from '../GenesisCollection/utils/contracts'
import { useNftApprove } from '../../hooks/useApprove'

interface RouteParams {
    id: string
}

interface LocationParams {
    creator?: string
    authorTwitter?: string
}


const GenesisCollectionDetail = () => {

    const { account } = useWallet()
    const [NFT, setNFT] = useState(0)
    const [sellPriceValue, setSellPrice] = useState(0)
    const [creator, setCreator] = useState('Shri')
    const [authorTwitter, setAuthorTwitter] = useState('')
    const [sellMinPriceValue, setSellMinPrice] = useState(0)
    const [mySoldNFT, setMySoldNFT] = useState(false)
    const [nftSecondaryMarketData, setNftSecondaryMarketData] = useState({})
    const [nftId, setNftId] = useState(0);
    const [isListed, setIsListed] = useState(false)
    const [pendingTx, setPendingTx] = useState(false)

    const params = useParams<RouteParams>();

    const location = useLocation();

    const { id } = params;

    const sellPrice = new URLSearchParams(location.search).get("sellPrice");
    const orderId = new URLSearchParams(location.search).get("orderId");

    const contraToken = useLifeContract(getLifeAddress())
    const allowance = useNftAllowance(contraToken, NftLiteMarketPlace, pendingTx)
    
    const onApprove = useNftApprove(contraToken, NftLiteMarketPlace)
    
    let nftDetail;
    let previewImage;
    let name;
    let description;
    let rarity;
    if(id) {
        const nftDetailArray = nfts.filter((data) => data.tokenId === parseInt(id));
        nftDetail = nftDetailArray && nftDetailArray[0];
        previewImage = nftDetail.previewImage;
        name = nftDetail.name;
        description = nftDetail.description;
        rarity = nftDetail.rarity
    }
    

    const handleSuccess = () => {
        return true;
    }

    const [onPresentBuyModal] = useModal(
        <BuyNftModal nft={nftDetail} orderId={parseInt(orderId)} sellPrice={parseInt(sellPrice)} onSuccess={handleSuccess} onDismiss = {handleSuccess} />,
    )

    const [onPresentSellModal] = useModal(
        <SellNftModal nft={nftDetail} sellMinPrice={sellMinPriceValue} nftSecondaryMarket={nftSecondaryMarketData} onSuccess={handleSuccess} onDismiss = {handleSuccess}  />,
    )

    return (
        <section className="vcollectionsec">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12 col-md-12 mb-5 vchead">
                        <Link to="/genesis-collection" className="varrow-icon"><img src="/assets/images/ArrowLeft.svg" alt='' />Genesis Collection</Link>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 col-md-6">
                        <div className="details-img">
                            <img src={`/images/nfts/${previewImage}`} alt="Vitruvian Androgyne" className="img-fluid" />
                        </div>
                    </div>
                    <div className="col-sm-12 col-md-6">
                        <div className="details-block">
                            <div className="details-head">
                                <div>
                                    <h6 className="inner-title">{name}</h6>
                                    {/* <p>Edition 2 of 6</p> */}
                                </div>
                                {/* Removed for now. */}
                                {/* <ul>
                                    <li><Link to="/"><img src="/assets/images/staricon.svg" alt="icon" className="img-fluid" /></Link></li>
                                    <li><Link to="/"><img src="/assets/images/share-icon.svg" alt="icon" className="img-fluid" /></Link></li>
                                </ul> */}
                            </div>
                            <div className=''>
                                <p>“{description}”</p>
                            </div>
                            {/* <div className="bidbox">
                                <ul>
                                    <li>
                                        <div className="bidcontent">
                                            <div className="bid-left">
                                                <p>Highest bid by <Link to="/">0x1b12n10...n128</Link></p>
                                                <h6>100 VLAD <span>$56.49</span></h6>
                                            </div>
                                            <div className="bid-right">
                                                <p>Aution ends in</p>
                                                <h6>24d  8h  14m  25s</h6>
                                            </div>
                                        </div>
                                    </li>
                                </ul>
                            </div> */}

                            {/* TODO:: Uncomment when data is available. */}
                            {/* <p className="service-fee">Service fee: <span>5%</span></p> */}

                            {account && allowance > sellPrice ? <ul>
                                {/* <li><Link to={`/genesis-detail/${id}`}  className="btn-link" data-bs-toggle="modal" data-bs-target="#buyvlad" onClick={onPresentTransferModal}>Buy Now</Link></li> */}
                                <li>
                                    <button type="button" className="btn-link"  onClick={onPresentBuyModal}>
                                        Buy Now
                                    </button>
                                </li>
                                {/* <li><Link to="/" className="btn-link" data-bs-toggle="modal" data-bs-target="#placeabid">PLACE A BID</Link></li> */}
                            </ul>
                            :
                            account && 
                            <ul>
                                <li>
                                    <button type="button" className="btn-link"  onClick={onPresentSellModal}>
                                        Sell Now
                                    </button>
                                </li>
                            </ul> 
                        }

                        {account && allowance < sellPrice && <ul>
                                <li>
                                    <button className="btn-link" type="button" onClick={async () => {
                                        try {
                                        setPendingTx(true)
                                        await onApprove()
                                        setPendingTx(false)
                                        } catch (e) {
                                        setPendingTx(false)
                                        console.error(e)
                                        }
                                    }}>
                                        Approve Now
                                    </button>
                                </li>
                            </ul> 
                        }

                            {/* Modal */}
                            <div className="ctm-modal modal fade" id="placeabid" tabIndex={-1} aria-labelledby="exampleModalLabel" aria-hidden="true">
                                <div className="modal-dialog modal-dialog-centered">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title" id="exampleModalLabel">Place a Bid</h5>
                                            <button type="button" className="btn-close ctm-btnclose" data-bs-dismiss="modal" aria-label="Close">CANCEL</button>
                                        </div>
                                        <div className="modal-body">
                                            <p>Your bid for <span>Vitruvian Androgyne</span></p>
                                            <div className="vladamount-wrap">
                                                <div className="vbalance">
                                                    <input type="text" className="form-control" placeholder='0.0' />
                                                </div>
                                                <div className="vladamount">
                                                    <span>Vlad <span>($0.0)<img src="/assets/images/vladsm.svg" alt="vlad logo" /></span>
                                                    </span></div>
                                            </div>
                                            <div className="amtrequired">Must be at least <span>100 VLAD ($56.49)</span></div>
                                            <hr />
                                            <div className="amt-wrap">
                                                <div className="amt-title">
                                                    <span>Your VLAD balance</span>
                                                    <span>Service fee (5%)</span>
                                                    <span>You will pay</span>
                                                </div>
                                                <div className="amt-bal">
                                                    <span>86.001 VLAD</span>
                                                    <span>0.501 VLAD</span>
                                                    <span>0.501 VLAD</span>
                                                </div>
                                            </div>
                                            <div className="btn-bidwrap">
                                                <button type='button' className="btn btn-primary" data-bs-target="#placeabid2" data-bs-toggle="modal" data-bs-dismiss="modal">Place a bid</button>
                                            </div>
                                            <div className="alertnote">
                                                <p><img src="/assets/images/alert-triangle.svg" alt="alert icon" />Not enough funds</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* Second Modal */}
                            <div className="ctm-modal modal fade" id="placeabid2" aria-hidden="true" aria-labelledby="exampleModalToggleLabel2" tabIndex={-1}>
                                <div className="modal-dialog modal-dialog-centered">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title" id="exampleModalLabel">Place a Bid</h5>
                                            <button type="button" className="btn-close ctm-btnclose" data-bs-dismiss="modal" aria-label="Close">CANCEL</button>
                                        </div>
                                        <div className="modal-body">
                                            <p>Your bid for <span>Vitruvian Androgyne</span></p>
                                            <div className="vladamount-wrap">
                                                <div className="vbalance"><input type="text" className="form-control" placeholder='0.0' /></div>
                                                <div className="vladamount">
                                                    <span>Vlad <span>($69.42)<img src="/assets/images/vladsm.svg" alt="vlad logo" /></span>
                                                    </span></div>
                                            </div>
                                            <div className="amtrequired">Must be at least <span>100 VLAD ($56.49)</span></div>
                                            <hr />
                                            <div className="amt-wrap">
                                                <div className="amt-title">
                                                    <span>Your VLAD balance</span>
                                                    <span>Service fee (5%)</span>
                                                    <span>You will pay</span>
                                                </div>
                                                <div className="amt-bal">
                                                    <span>86.001 VLAD</span>
                                                    <span>0.501 VLAD</span>
                                                    <span>169.501 VLAD</span>
                                                </div>
                                            </div>
                                            <div className="btn-bidwrap">
                                                <button type='button' className="btn btn-primary activebuy" data-bs-target="#placeabid3" data-bs-toggle="modal" data-bs-dismiss="modal">Next Step</button>
                                            </div>
                                            <div className="additionalnote">
                                                <p>5% fee will go to the artist and marketplace maintenance</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* Third Modal */}
                            <div className="ctm-modal modal fade" id="placeabid3" aria-hidden="true" aria-labelledby="exampleModalToggleLabel2" tabIndex={-1}>
                                <div className="modal-dialog modal-dialog-centered">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title" id="exampleModalLabel">Place a Bid</h5>
                                            <button type="button" className="btn-close ctm-btnclose" data-bs-dismiss="modal" aria-label="Close">CANCEL</button>
                                        </div>
                                        <div className="modal-body">
                                            <p><span className="cwhite">Approve </span> the transaction from your wallet to continue.</p>
                                            <hr />
                                            <div className="btn-bidwrap">
                                                <button type='button' className="btn btn-primary cred btn-pending" data-bs-target="#placeabid4" data-bs-toggle="modal" data-bs-dismiss="modal"><i><img src="/assets/images/loader.svg" alt="loader" /></i>PENDING APPROVAL</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* Fourth Modal */}
                            <div className="ctm-modal modal fade" id="placeabid4" aria-hidden="true" aria-labelledby="exampleModalToggleLabel2" tabIndex={-1}>
                                <div className="modal-dialog modal-dialog-centered">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title" id="exampleModalLabel">Place a Bid</h5>
                                            <button type="button" className="btn-close ctm-btnclose" data-bs-dismiss="modal" aria-label="Close">CANCEL</button>
                                        </div>
                                        <div className="modal-body">
                                            <p><span className="cwhite">Sign </span>and set your bid expiry time</p>
                                            <div className="exptime">
                                                <ul>
                                                    <li><Link to="/" className="expbtnactive">3 Days</Link></li>
                                                    <li><Link to="/">7 Days</Link></li>
                                                    <li><Link to="/">30 Days</Link></li>
                                                    <li><Link to="/">1 Year</Link></li>
                                                </ul>
                                            </div>
                                            <div className="text-center">
                                                <p className="cgrey">Your bid will expire on <span className="expday">Jul 12</span> <span className="exphour"> @ 23:34pm</span></p>
                                                <hr />
                                                <p className="cwhite">You’re placing a bid for 169.501 VLAD</p>
                                                <div className="btn-bidwrap">
                                                    <button type='button' className="btn btn-primary cred btn-pending" data-bs-target="#placeabid5" data-bs-toggle="modal" data-bs-dismiss="modal"><i><img src="/assets/images/loader.svg" alt="loader" /></i>PLACE BID</button>
                                                </div>
                                                <p className="cgrey">When a bid is placed, your VLAD remains in your wallet. Vlad Finance marketplace smart contracts don&apos;t hold your VLAD. Keep it in your wallet if you want the bid to stay valid.</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* Fifth Modal BID SUCCESS */}
                            <div className="ctm-modal modal fade" id="placeabid5" aria-hidden="true" aria-labelledby="exampleModalToggleLabel2" tabIndex={-1}>
                                <div className="modal-dialog modal-dialog-centered">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title" id="exampleModalLabel">Place a Bid</h5>
                                            <button type="button" className="btn-close ctm-btnclose" data-bs-dismiss="modal" aria-label="Close">CANCEL</button>
                                        </div>
                                        <div className="modal-body success-body">
                                            <img src="/assets/images/check-circle.svg" alt="success icon" />
                                            <p>Your bid has been placed!</p>
                                            <p className="cgrey">Please keep <span>[bid amount] </span>available in your wallet for the bid to be valid.</p>
                                            <hr />
                                            <p>Your bid will expire on Jul 12 @ 23:34pm</p>
                                            <div className="btn-bidwrap">
                                                <button type='button' className="btn btn-primary activebuy" data-bs-target="#transactionfailed" data-bs-toggle="modal" data-bs-dismiss="modal">Done</button>
                                            </div>
                                            <div className="additionalnote">
                                                <p className="cwhite">Your bid will expire on <span className="expday">Jul 12</span> <span className="exphour"> @ 23:34pm</span></p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* Fifth Modal BID SUCCESS */}
                            <div className="creator-wrap">
                                <div className="creator">
                                    <p>Creator</p>
                                    <Link to={{ pathname: authorTwitter }}>{creator}</Link>
                                </div>
                                <div className="crecollection">
                                    <p>COLLECTION</p>
                                    <Link to="/genesis-collection">Genesis Collection</Link>
                                </div>
                                <div className="clear-fix" />
                            </div>
                            {/* Start of Small Tab */}
                            <div className="smalltab">
                                <ul className="smalltab nav nav-pills mb-3" id="pills-tab" role="tablist">
                                    <li className="nav-item" role="presentation">
                                        <button className="nav-link smtablink active" id="pills-home-tab" data-bs-toggle="pill" data-bs-target="#pills-home" type="button" role="tab" aria-controls="pills-home" aria-selected="true">DETAILS</button>
                                    </li>
                                    {/* <li className="nav-item" role="presentation">
                                        <button className="nav-link smtablink" id="pills-profile-tab" data-bs-toggle="pill" data-bs-target="#pills-profile" type="button" role="tab" aria-controls="pills-profile" aria-selected="false">OFFERS</button>
                                    </li>
                                    <li className="nav-item" role="presentation">
                                        <button className="nav-link smtablink" id="pills-contact-tab" data-bs-toggle="pill" data-bs-target="#pills-contact" type="button" role="tab" aria-controls="pills-contact" aria-selected="false">HISTORY</button>
                                    </li> */}
                                </ul>
                                <div className="sm-tabcontent tab-content" id="pills-tabContent">
                                    <div className="tab-pane fade show active" id="pills-home" role="tabpanel" aria-labelledby="pills-home-tab">
                                        <table className="table table-borderless tabtable">
                                            <tbody>
                                                <tr>
                                                    <td className="tbldata-grey">Trade ID</td>
                                                    <td>#{id}</td>
                                                </tr>
                                                <tr>
                                                    <td className="tbldata-grey">Rarity Tier</td>
                                                    <td>{rarity}</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                    <div className="tab-pane fade" id="pills-profile" role="tabpanel" aria-labelledby="pills-profile-tab">
                                        <div>
                                            <p className="offer">{new BigNumber(sellPriceValue).div(new BigNumber(10).pow(18)).toFixed()} VLAD <span>by</span> <Link to="/">0x1b12n10...n128</Link></p>
                                            <p className="time"><span>7/6/2021,</span><span>1:01 PM</span></p>
                                        </div>
                                    </div>
                                    <div className="tab-pane fade" id="pills-contact" role="tabpanel" aria-labelledby="pills-contact-tab">
                                        <div className="historytab">
                                            <ul>
                                                <li className="thumb">
                                                    <div className="historylist">
                                                        <p className="boldletter">Bid accpeted <span className="whiteup">10 VLAD</span></p>
                                                        <p>By <span><Link to='/'>0x1b...6666 </Link></span>at <span>7/8/2021,</span><span> 9:55 PM</span></p>
                                                    </div>
                                                </li>
                                                <li className="mintlayer">
                                                    <div className="historylist">
                                                        <p className="boldletter">Bid <span className="whiteup">10 VLAD</span></p>
                                                        <p>By <span><Link to='/'>0x1b...6666 </Link></span>at <span>7/8/2021,</span><span> 9:55 PM</span></p>
                                                    </div>
                                                </li>
                                                <li className="mintlayer2">
                                                    <div className="historylist">
                                                        <p className="boldletter">Transfered to <Link to="/">Sushinobi</Link></p>
                                                        <p><span>7/8/2021,</span><span> 9:55 PM</span></p>
                                                    </div>
                                                </li>
                                                <li className="updown">
                                                    <div className="historylist">
                                                        <p className="boldletter">Minted by <Link to="/">Sushinobi</Link></p>
                                                        <p><span>7/8/2021,</span><span> 9:55 PM</span></p>
                                                    </div>
                                                </li>
                                                <li className="mintcircle">
                                                    <div className="historylist">
                                                        <p className="boldletter">Bid accpeted <span className="whiteup">10 VLAD</span></p>
                                                        <p>By <span><Link to='/'>0x1b...6666 </Link></span>at <span>7/8/2021,</span><span> 9:55 PM</span></p>
                                                    </div>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    );

};

export default GenesisCollectionDetail;
