import React, { useState } from 'react'
import styled from 'styled-components'
import Web3 from 'web3'
import _ from 'lodash';
import BigNumber from 'bignumber.js'
import { Link } from 'react-router-dom';
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Button, Input, Text } from '@pancakeswap-libs/uikit'
import { getBalanceNumber } from 'utils/formatBalance'
import { getLifeAddress } from 'utils/addressHelpers'
import useTokenBalance from 'hooks/useTokenBalance'
import { NFT, NftFarm } from 'config/constants/genesisNfts'
import { GenesisNft } from 'config/constants/types'
import useI18n from 'hooks/useI18n'
import { useNftLiteMarketplaceContract } from 'hooks/useContract'

interface SellNftModalProps {
  nft: GenesisNft
  sellMinPrice: number
  nftSecondaryMarket: any
  onSuccess: () => any
  onDismiss: () => void
}


const Actions = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 8px;
`

const SellNftModal: React.FC<SellNftModalProps> = ({ nft, sellMinPrice, nftSecondaryMarket, onSuccess, onDismiss }) => {

  const [isLoading, setIsLoading] = useState(false)
  const { account } = useWallet()

  
  const vladBalance = useTokenBalance(getLifeAddress());
  const vladInWallet = getBalanceNumber(vladBalance)

  const { totalArtistFee, totalDevFee, totalGovernanceFee } = nftSecondaryMarket;


  const [values, setValues] = useState({
    address: account,
    sellPrice: 0
  })

  
  const totalFee = +(_.sum([parseInt(totalDevFee, 10), parseInt(totalArtistFee, 10), parseInt(totalGovernanceFee, 10), values.sellPrice]));

  const [error, setError] = useState(null)
  const TranslateString = useI18n()

  const nftLiteMarketplaceContract = useNftLiteMarketplaceContract(NftFarm)

  const handleConfirm = async () => {
    try {
      const isValidAddress = Web3.utils.isAddress(values.address)

      const sellPriceValue = ((values.sellPrice * (10 ** 18)));
      const sellMinPriceValue = ((sellMinPrice));

      if (!isValidAddress) {
        setError(TranslateString(999, 'Please enter a valid wallet address'))
      } else if(sellPriceValue < sellMinPriceValue) {
        setError(TranslateString(999, 'Please enter sell price greater than min sell price'))
      } else {
        await nftLiteMarketplaceContract.methods
          .sell(NFT, nft.tokenId, new BigNumber(values.sellPrice).times(new BigNumber(10).pow(18)).toString())
          .send({ from: account })
          .on('sending', () => {
            setIsLoading(true)
          })
          .on('receipt', () => {
            onDismiss()
            onSuccess()
          })
          .on('error', () => {
            console.error(error)
            setError('Unable to sell NFT')
            setIsLoading(false)
          })
          window.location.reload();
      }
    } catch (err) {
      console.error('Unable to sell NFT:', err)
    }
  }

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const { value: inputValue, name } = evt.target
    setValues({ ...values, [name]: inputValue })
  }

  return (
    <div className="ctm-modal modal fade show d-block">
      <div className="modal-dialog modal-dialog-centered ">
          <div className="modal-content">

            <div className="modal-header">
              <h5 className="modal-title text-color-white">Sell Now</h5>
              <button type="button" className="btn-close ctm-btnclose" data-bs-dismiss="modal" aria-label="Close" onClick={onDismiss}>CANCEL</button>
            </div>
            {error && (
              <Text color="failure" mb="8px">
                {error}
              </Text>
            )}
            <div className="modal-body">
              <p>Your are selling <span>ALIFE Token</span></p>
              <div className="bidbox">
                <ul>
                  <li>
                    <div className="bidcontent">
                      <div className="vbid-details">
                      <h6><span className="vladinput">
                          <Input
                            className="form-control"
                            id="sellPrice"
                            name="sellPrice"
                            type="number"
                            placeholder={TranslateString(999, 'Enter selling price')}
                            value={values.sellPrice}
                            onChange={handleChange}
                            isWarning={error}
                            disabled={isLoading}
                          />
                        </span>
                        </h6>
                        <h6 className="text-color-white">
                          <span className="ctmtooltip">
                            {/* <Link to="/" className="ctmtooltip"> */}
                            <img src="/assets/images/upgraph.svg" alt="" />BONDING CURVE
                            <div className="ctmtooltiptext">
                              <h6 className="title"><img src="/assets/images/upgraph.svg" alt="" />BONDING CURVE</h6>
                              <hr />
                              <p>A bonding curve is a mathematical curve that defines the relationship between the price and the supply of a given asset.</p>
                              <p className="text-deem">After the sale of each NFT, the price of the next piece will automatically increase by a percentage calculated by the bonding curve.</p>
                            </div>
                            {/* </Link> */}
                          </span>
                        </h6>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
              <hr />
              <div className="amt-wrap">
                <div className="amt-title">
                  <span>Your ALIFE balance</span>
                  <span>Developer Fee</span>
                  <span>Artist Fee</span>
                  <span>Governance Fee</span>
                  <span>You will get</span>
                </div>
                <div className="amt-bal">
                  <span>{_.round(vladInWallet)} ALIFE</span>
                  <span>{totalDevFee} ALIFE</span>
                  <span>{totalArtistFee} ALIFE</span>
                  <span>{totalGovernanceFee} ALIFE</span>
                  <span>{totalFee} ALIFE</span>
                </div>
              </div>
              <div className="btn-bidwrap">
                <Actions>
                    <Button fullWidth onClick={onDismiss} >
                      {TranslateString(462, 'Cancel')}
                    </Button>
                    <Button fullWidth onClick={handleConfirm} disabled={!account || isLoading || !values.address || !values.sellPrice} >
                      Sell now
                    </Button>
                </Actions>
              </div>
            
            </div>
          </div>
      </div>
    </div>
  
  )
}

export default SellNftModal
