import React, { useState } from 'react'
import styled from 'styled-components'
import Web3 from 'web3'
import _ from 'lodash';
import BigNumber from 'bignumber.js'
import { Link } from 'react-router-dom';
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Button, Input, Modal, Text } from '@pancakeswap-libs/uikit'
import { NFT, NftFarm, NftLiteMarketPlace } from 'config/constants/genesisNfts'
import { getBalanceNumber } from 'utils/formatBalance'
import { getLifeAddress } from 'utils/addressHelpers'
import useTokenBalance from 'hooks/useTokenBalance'
import { GenesisNft } from 'config/constants/types'
import useI18n from 'hooks/useI18n'
import { useNftLiteMarketplaceContract } from 'hooks/useContract'

interface BuyNftModalProps {
  nft: GenesisNft
  orderId: number
  sellPrice: number
  onSuccess: () => any
  onDismiss: () => void
}

const Value = styled(Text)`
  font-weight: 600;
`

const ModalContent = styled.div`
  margin-bottom: 16px;
`

const Actions = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 8px;
`

const Label = styled.label`
  color: ${({ theme }) => theme.colors.text};
  display: block;
  margin-bottom: 8px;
  margin-top: 24px;
`

const BuyNftModal: React.FC<BuyNftModalProps> = ({ nft, orderId, sellPrice, onSuccess, onDismiss }) => {
  const [isLoading, setIsLoading] = useState(false)
  
  const [error, setError] = useState(null)
  const TranslateString = useI18n()
  const { account } = useWallet();
  
  const vladBalance = useTokenBalance(getLifeAddress());
  const vladInWallet = getBalanceNumber(vladBalance);

  const [values, setValues] = useState({
    address: account,
    orderId
  })

  const isBuyDisabled =  new BigNumber(sellPrice) > new BigNumber(vladInWallet * (10**18));

  const nftLiteMarketplaceContract = useNftLiteMarketplaceContract(NftLiteMarketPlace)

  const handleConfirm = async () => {
    try {
      const isValidAddress = Web3.utils.isAddress(values.address)

      if (!isValidAddress) {
        setError(TranslateString(999, 'Please enter a valid wallet address'))
      } else {
        await nftLiteMarketplaceContract.methods
          .buy(NFT, nft.tokenId, values.orderId)
          .send({ from: account })
          .on('sending', () => {
            setIsLoading(true)
          })
          .on('receipt', () => {
            console.log('receipt')
          })
          .on('error', () => {
            setError('Unable to buy NFT')
            setIsLoading(false)
          })
          window.location.reload();
      }
    } catch (err) {
      console.error('Unable to buy NFT:', err)
    }
  }

  return (
    <div className="ctm-modal modal fade show d-block">
      <div className="modal-dialog modal-dialog-centered ">
          <div className="modal-content">

            <div className="modal-header">
              <h5 className="modal-title text-color-white">Buy Now</h5>
              <button type="button" className="btn-close ctm-btnclose" data-bs-dismiss="modal" aria-label="Close" onClick={onDismiss}>CANCEL</button>
            </div>
            {error && (
              <Text color="failure" mb="8px">
                {error}
              </Text>
            )}
            <div className="modal-body">
              <p>Your are buying <span>ALIFE Token</span></p>
              <div className="bidbox">
                <ul>
                  <li>
                    <div className="bidcontent">
                      <div className="vbid-details">
                        <h6 className="text-color-white">
                          <span className="ctmtooltip">
                            {/* <Link to="/" className="ctmtooltip"> */}
                            <img src="/assets/images/upgraph.svg" alt="" />BONDING CURVE
                            <div className="ctmtooltiptext">
                              <h6 className="title"><img src="/assets/images/upgraph.svg" alt="" />BONDING CURVE</h6>
                              <hr />
                              <p>A bonding curve is a mathematical curve that defines the relationship between the price and the supply of a given asset.</p>
                              <p className="text-deem">After the sale of each NFT, the price of the next piece will automatically increase by a percentage calculated by the bonding curve.</p>
                            </div>
                            {/* </Link> */}
                          </span>
                        </h6>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
              <hr />
              <div className="amt-wrap">
                <div className="amt-title">
                  <span>Your ALIFE balance</span>
                  <span>Service fee (5%)</span>
                  <span>You will pay</span>
                </div>
                <div className="amt-bal">
                  <span>{_.round(vladInWallet)} ALIFE</span>
                  <span>{(sellPrice / (10**18)) * 0.05} ALIFE</span>
                  <span>{(sellPrice / (10**18)) + (sellPrice / (10**18)) * 0.05} ALIFE</span>
                </div>
              </div>
              <div className="btn-bidwrap">
                <Actions>
                    <Button fullWidth onClick={onDismiss} >
                      {TranslateString(462, 'Cancel')}
                    </Button>
                    <Button fullWidth onClick={handleConfirm} disabled={ isBuyDisabled} >
                      Buy now
                    </Button>
                </Actions>
              </div>
              {new BigNumber(sellPrice) > new BigNumber(vladInWallet * (10**18)) && <div className="alertnote">
                <p><img src="/assets/images/alert-triangle.svg" alt="alert icon" />Not enough funds</p>
              </div>  }
              
            </div>
          </div>
      </div>
    </div>
  )
}

export default BuyNftModal
